import React from 'react';
import { Link } from 'react-router-dom';

import logo from '../../assets/logo_desk.svg';
import { routes } from '../../pages/routes';
import {
  HeaderContainer,
  HeaderLogo,
  HeaderLinks,
  StyledLink,
  IconLink,
} from '../UI/Header';
import { IconFaceBgColor, IconYoutubeBgColor } from '../UI/Icons';

export const Navbar = () => {
  return (
    <HeaderContainer>
      <Link to='/'>
        <HeaderLogo src={logo} alt='Logo' />
      </Link>
      <HeaderLinks>
        {routes
          // ignoring the default path & home
          .filter(
            (route) =>
              route.path === '/curso' ||
              route.path === '/talleristas' ||
              route.path === '/mapa-curicular'
          )
          .map((route) => (
            <StyledLink
              data-link='nav-link'
              key={route.path}
              to={route.path}
              exact
              activeClassName='current'
            >
              {route.name}
            </StyledLink>
          ))}
        <IconLink href='link' target='_black'>
          <IconFaceBgColor />
        </IconLink>
        <IconLink href='link' target='_black'>
          <IconYoutubeBgColor />
        </IconLink>
      </HeaderLinks>
    </HeaderContainer>
  );
};
